<template>
  <div>
    <breadcrumb-box>
      <div class="search">

        <el-input placeholder="请输入关键字" @keyup.enter.native="subjectfind" v-model="key" style="width: 250px;">
          <i slot="prefix" class="el-input__icon el-icon-search"></i>

        </el-input>
      </div>
    </breadcrumb-box>
    <div v-if="total> 0">
      <el-card shadow='hover' v-for="item in subject" :key="item.id">
        <div class="section">
          <div class="w750">
            <div class=" flex-space-between">
              <div class="section-left h1">
                <router-link :to="{ path: '/studio', query: { id: item.id,fromPage:'Home' }}">
                  {{item.title}}
                </router-link>
              </div>
              <div class="section-right FLEX" v-show="item.members.length >0">
                <div
                  v-for="(item , index)  in (item.members)" :key="item.id" v-if="index < 5">
                  <avatar :headIcon='item.user_headicon' :name='item.user_abbr' :bgColor='item.user_backcolor'
                          size="mini"></avatar>
                </div>
                <div>
                  <avatar :name='JSON.stringify(item.members.length)' :bgColor="color" size="mini"></avatar>
                </div>
              </div>
            </div>
            <router-link :to="{ path: '/studio', query: { id: item.id,fromPage:'Home' }}">
              <div class="section-main" style="cursor: pointer" v-html="item.content">
              </div>
            </router-link>
            <div class="section-footer">
              <span class="nick"> {{item.creator.user_name}}</span>

              <span class="updata-time"> 更新于 &nbsp;{{item.mtime}}</span>
            </div>
          </div>
        </div>
      </el-card>


      <el-card class="box-card" shadow='hover' v-show="subject.length > 0">
        <div class="more" @click="gomore">
          {{moreTxt}}
        </div>

      </el-card>
    </div>
    <div class="subject-box" v-if="isShow">
      <tip txt="未搜索到相关数据" oder="1"></tip>


    </div>

  </div>
</template>

<script>
  import {mapGetters} from '@icony/vue-container/vuex'

  import BreadcrumbBox from '@/layout/BreadcrumbBox'
  import {indexlist, subjectfind} from '@/api/index'
  import Avatar from '@/components/Avatar/index'
  import Tip from '@/components/Tip/index'

  export default {
    name: "Home",

    components: {
      BreadcrumbBox,
      Avatar,
      Tip
    },

    data() {
      return {
        limit: 10,
        page: 1,
        page1: 1,
        subject: [],
        px: '0px',
        moreTxt: '点击加载更多',
        key: '',
        flag: true,
        total: 0,
        color: '#AAB1C8',
        userId: this.$store.state.user.user.id,
        isShow:false


      }
    },
    computed: {
      ...mapGetters(["name"])
    },
    created() {
      this.indexlist()
    },
    methods: {
      indexlist() {
        indexlist({
          page: this.page,
          limit: this.limit,

        }).then((res) => {

          this.subject = this.subject.concat(res.data.list)
          this.total = res.data.total

          if (res.data.list.length < this.limit) {
            this.flag = false
            this.moreTxt = '已无更多'
          } else {
            this.flag = true
          }
          this.page = ++this.page
        })

      },
      subjectfind() {
        subjectfind({

          page: this.page1,
          limit: this.limit,
          key: this.key


        }).then((res) => {
          this.subject = []
          this.subject = this.subject.concat(res.data.list)
          this.total = res.data.total
          if(!(res.data.list.length >0) ){
            this.isShow = true
          }

          if (res.data.list.length < this.limit) {
            this.flag = false
            this.moreTxt = '已无更多'
          } else {
            this.flag = true
            this.page1 = ++this.page1

          }


        })

      },
      gomore() {
        if (this.flag) {
          if (this.key) {

            this.subjectfind()
          } else {
            this.indexlist()

          }

        }

      },

    }
  };
</script>

<style lang="scss" scoped>

  .section {

    background: #fff;
    // padding: 0px 0px 10px 0px;

  }

  .section-footer {
    color: rgba(0, 0, 0, 0.447058823529412);
  }

  .section-footer .nick {
    color: #1890FF;
  }

  .section-footer .updata-time {
    font-size: 12px;
    color: rgba(0, 0, 0, 0.247058823529412);
    margin-left: 5px;
  }
  .section-main{
    word-break: break-all;
    text-overflow: ellipsis;
    display: -webkit-box; /** 将对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp: 2; /** 显示的行数 **/
    overflow: hidden;  /** 隐藏超出的内容 **/

  }
  .section-main, .section-footer {
    margin-top: 15px;
    font-size: 14px;




  }

  .section-right {
    position: relative;

  }

  .avatarPosition {
    width: 40px;

    top: 0px;
    right: 0px;
    position: absolute;

  }

  .more {
    text-align: center;
    cursor: pointer;
  }

  .subject-box {
    height: 550px;
    width: 100%;

    background: #ffffff;
    overflow: hidden;

  }

</style>





